<template>
    <div class="Settlement">
        <div class="Settlement-address">
            <p class="bold tile">选择收货地址</p>
            <div class="add-select clearfix">
                <ul class="addlist">
                    <li :class="[{DefaultAdd:DefaultAddress === k},'add-item', 'fl', 'cursor']"
                        v-for="(i,k) in AddList"
                        @click="defaultAdd(k)" :key=k>
                        <div class="user-name">
                            {{i.truename}}
                            <a  class="fr ac" v-if="i.moren">默认地址</a>
                            <a  class="fr ac" v-else @click="setDefaultAddress(i.id)">设为默认</a>
                        </div>
                        <div class="address">
                            <p>{{i.telephone}}</p>
                            <p>{{i.address}}</p>
                        </div>
                        <img v-show="DefaultAddress === k" src="../../assets/img/order/icon_checked.png" alt="">
                        <!--<a class="edit">修改</a>-->
                    </li>
                    <li class="add-new fl cursor" @click="add=true">
                        <img src="../../assets/img/icon_add_to.png" alt="">
                        <p class="ac">添加新地址</p>
                    </li>
                </ul>
            </div>
        </div>

        <div class="goods">
            <p class="bold tile">商品信息</p>
            <table class="good-detail">
                <thead>
                    <tr>
                        <td class="ac sm-font tile">商品详情</td>
                        <td class="ac sm-font price">单价</td>
                        <td class="ac sm-font num">数量</td>
                        <td class="ac sm-font total">小计</td>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(i,k) in goods" :key=k>
                        <td style="padding: 20px">
                            <img style="width: 60px;height: 60px;display: inline-block" :src="i.default_img | img" alt="">
                            <a :href="`/goods.html#/${i.product_id}`" target="_blank">
                                <p style="width: 250px;display: inline-block;margin-left: 20px">
                                    {{i.product_name}}
                                </p>
                            </a>
                        </td>
                        <td class="ac">
                            {{i|exch(ex)}}{{ex.unit}}
                        </td>
                        <td class="ac">
                            <div>
                                <a class="inc-btn" @click="changeNum(-1,i)">-</a>
                                <input class="num" type="number" v-model="i.nums">
                                <a class="inc-btn" @click="changeNum(1,i)">+</a>
                            </div>
                        </td>
                        <td class="ac">
                            {{i|exch(ex) * i.nums|fixed}}{{ex.unit}}
                        </td>
                    </tr>
                </tbody>
                <tfoot>
                    <tr>
                        <td colspan="2">
                            <div >
                                <span class="sm-font"
                                      style="position:absolute;top: 10px;left: 20px "
                                > 给卖家留言</span>
                                <textarea style="
                                    position: absolute;
                                    top: 10px;
                                    left: 90px;
                                    width: 400px;
                                    height: 80px;
                                    resize: none;
                                    padding: 10px;
                                "
                                    placeholder="留言信息"
                                    v-model="note"
                                    ></textarea>

                            </div>
                        </td>
                        <td colspan="2">
                            <div>
                                <span style="
                                    position: absolute;
                                    left: 20px;
                                    bottom: 15px;
                                ">合计：</span>
                                <span   class="lager-font bold"
                                    style="
                                    position: absolute;
                                    right: 20px;
                                    bottom: 15px;
                                    color: #ff0077;
                                ">
                                    {{TotalMoney/ex.exchange}}{{ex.unit}}
                                </span>
                                <!--<span-->
                                    <!--style="-->
                                    <!--position: absolute;-->
                                    <!--right: 20px;-->
                                    <!--bottom: 45px;-->
                                <!--">-->
                                    <!--运费：{{expressFee/ex.exchange}}{{ex.unit}}-->
                                <!--</span>-->
                            </div>
                        </td>
                    </tr>
                </tfoot>
            </table>

            <div class="pay-money ar">
                <p style="font-size: 16px;color: black;font-weight: bold;">您的账户还有{{user_point/ex.exchange.toFixed(2)}}{{ex.unit}},
                    <span v-if="PayPoint === 0">足够支付订单</span>
                    <span v-else>剩余部分将使用第三方支付</span>
                </p>
                <p>{{goodsNum}} 件商品，商品总金额：<span class="price theme-color bold">{{TotalMoney/ex.exchange}}{{ex.unit}}</span></p>
                <p>运费：<span class="price theme-color bold">{{expressFee/ex.exchange}}{{ex.unit}}</span></p>
                <p>需付款：<span class="price theme-color bold">{{TotalMoney/ex.exchange+expressFee/ex.exchange}}{{ex.unit}}</span></p>
                <a class="btn-sela ac bold" @click="createNewOrder">提交订单</a>
            </div>
        </div>
        <add-address :add="add"></add-address>
    </div>
</template>

<script>
    let display = {
        showRecommend: false,
        nav: 'hidden',
        header: 'fault'
    };
    import AddAddress from '../../components/add-address'
    import {mapState} from 'vuex'
    export default {
        name: 'Settlement',
        metaInfo: {
            title: '订单结算',
            meta: [
                {name: 'keywords', content: '福分通'},
                {name: 'description', content: '专注企业员工福利'}
            ]
        },
        components:{
            AddAddress
        },
        data() {
            return {
                add:false,
                AddList:[],
                user_point:'',
                DefaultAddress: 0,
                dikoujifen:'',
                DefaultPayment:'JifenPay',
                goods:[],
                expressFee:0,
                coupon_id:null,//优惠券ID
                note:'',//留言信息
                isDikou:false,//JifenPay,AliPay,WeChatPay,isDikou
                payment:[
                    {title:'余额支付',type:'JifenPay'},
                    {title:'支付宝',type:'Alipay'},
                    {title:'微信支付',type:'WeChatPay'}
                ],
                ex:{
                    exchange:'',
                    unit:'积分'
                }
            }
        },
        filters:{
            fixed(item){
                return item.toFixed(2)
            }
        },
        async mounted(){
            this.bus.$on('add',function (a) {
                this.add = false;
                this.getAddressList()
            }.bind(this));
            if (this.$route.query.goods){
                await this.getGoodsInfo();
                await this.getAddressList();
            }else{
                this.goods = this.shopping_cart;
                await this.getAddressList();
                if (this.goods.length=== 0){
                    this.$router.go(-1)
                }
            }
        },
        created() {
            this.$store.dispatch('layout/Change', display);

            this.getMemberInfo()
        },
        methods:{
            getAddressList(){
                this.yum('api/address/lists').then(res=>{
                    this.ex.exchange = parseInt(res.data.exchange);
                    this.ex.unit = res.data.unit;
                    if (res.data.status === 200) {
                        this.AddList = res.data.data;
                        this.AddList.forEach((item,key)=>{
                            if (item.moren){
                                this.DefaultAddress = key;
                            }
                        });
                        this.getExpressFee()
                    }else {
                        this.bus.$emit('notice', res.data.msg)
                    }
                })
            },
            setDefaultAddress(id){
                this.yum('api/address/setDefault',{address_id:id}).then(res=>{
                    if (res.data.status === 200){
                        this.bus.$emit('notice',res.data.msg);
                        this.getAddressList()
                    }
                })
            },
            defaultAdd(k){
                this.DefaultAddress = k;
                this.getExpressFee()
            },
           async getGoodsInfo(){
                let data = {product_id:this.$route.query.goods};
                await this.yum('api/Product/getGoodsInfo',data).then(res=>{
                    if (res.data.status === 200){
                        this.goods = [];
                        res.data.data.forEach((item,index)=>{
                            let good = Object.assign({nums:1},item);
                            this.goods.push(good)
                        })
                    }
                })
            },
            changeNum(a,b){
                if (b.nums<2&&a<0){
                    return
                }
                else{
                    b.nums = parseInt(b.nums)+a;
                    this.getExpressFee()
                }
            },
            async getExpressFee(){
                let data = {
                    province:this.AddList[this.DefaultAddress].province,
                    goods:[]
                };
                this.goods.forEach((item,key)=>{
                    data.goods.push(`${item.product_id}_${item.nums}`)
                });
                this.yum('api/order/expressFee',data).then(res=>{
                    this.expressFee = res.data.data
                })
            },
            getMemberInfo(){
                this.yum('api/member/getMemberInfo').then(res=>{
                    if (res.data.status === 200){
                        this.user_point = res.data.data.point
                    }
                })
            },
            createNewOrder(){
                if(this.AddList.length === 0){
                    this.bus.$emit('notice','请添加送货地址')
                    return
                }
                let data = {
                    address_id:this.AddList[this.DefaultAddress].id||'',
                    note:this.note,
                    pay_point:this.PayPoint,
                    goods:[]
                };
                if (this.coupon_id!=null){
                    data = Object.assign(data,{member_coupon_id:this.coupon_id})
                };
                this.goods.forEach(item=>{
                    data.goods.push(`${item.product_id}_${item.nums}`)
                });
                this.yum('api/order/createorder',data).then(res=>{
                    if (res.data.status === 200){
                        this.$router.push({
                            path:'/paytable',
                            query:{
                                order_sn:res.data.data
                            }
                        })
                    }else{
                        this.bus.$emit('notice',res.data.msg)
                    }
                })
            }
        },
        computed:{
            TotalMoney:{
                get(){
                    let total = 0;
                    this.goods.forEach(item=>{
                        total = total+item.jifen*item.nums
                    });
                    return total
                }
            },
            ...mapState('CART',['shopping_cart']),
            goodsNum(){
                let num = 0;
                this.goods.forEach(item=>{
                    num+=parseInt(item.nums)
                });
                return num
            },
            PayPoint(){
                if(this.TotalMoney+this.expressFee<=parseInt(this.user_point)){
                    return 0
                }else {
                    return parseFloat(this.user_point)
                }
            }
        }
    }
</script>

<style lang="stylus" scoped>
.tile
    font-size 16px
    color black
    margin-bottom 20px
.Settlement
    min-width 100%
    margin 40px 0 0 0
    position relative
    &.router
        margin-left 0
    .add-select
        width 990px
        margin-bottom 30px
        li
            padding 0 10px
            width 293px
            height 110px
            display inline-block
            border 2px dashed #ccc
            margin-right 8px
            margin-bottom 8px
            &.DefaultAdd
                border-color #ff0077
        .add-item
            position relative
            div.user-name
                border-bottom 1px solid #ccc
                height 30px
                line-height 30px
                a
                    display inline-block
                    width 60px
                    height 24px
                    line-height 24px
                    margin-top 3px
                    font-size 12px
                    background-color #999
                    color white
            img
                position absolute
                bottom -2px
                right -2px
            div.address
                padding-top 10px
                position relative
            a.edit
                position absolute
                color #00a6ff
                right 30px
                bottom 10px
        .add-new
            position relative
            img
                margin 30px auto 10px
                display block
            p
                font-size 12px

    .choose-payment
        position relative
        margin-bottom 30px
        ul
            position relative
            margin-bottom 10px
            height 28px
        li
            position relative
            display inline-block
            width 100px
            height 26px
            line-height 22px
            border 2px solid #ccc
            margin-right 10px
            &.selected-pay
                border-color #ff0077
            img
                position absolute
                bottom -2px
                right -2px
            &.mixed-payment
                border none
                width auto
            input[type=checkbox]
                width 20px
                height 20px
                margin-left 5px
                vertical-align middle
            input[type=text]
                height 26px
                margin-left 5px
    .goods
        position relative
        table.good-detail
            width 100%
            margin-bottom 30px
            td
                border 1px solid #ccc
            a.inc-btn
                width 20px
                height 30px
                line-height 30px
                display inline-block
                background-color #ccc
                user-select none
            input.num
                width 50px
                height 30px
                text-align center
                -moz-appearance:textfield;
                &::-webkit-outer-spin-button,&::-webkit-inner-spin-button
                    -webkit-appearance: none !important;
            thead
                background-color #e4e4e4
                height 40px
                td
                    height 40px
                    &.tile
                        width 490px
                    &.price
                        width 120px
                    &.num
                        width 120px
            tfoot
                height 100px
                td
                    height 100px
                    div
                        position relative
                        height 100px
        .pay-money
            height 210px
            p
                line-height 25px
            span.price
                font-size 16px
                width 100px
            .btn-sela
                display inline-block
                width 120px
                height 60px
                line-height 60px
                font-size 20px
                background-color #ff0077
                color white
                margin-top 30px
</style>
