<template>
    <div class="add-address" v-if="add">
        <div class="insert-address">
            <div class="close cursor" @click="function() {
              bus.$emit('add',false)
            }">
                <img src="../assets/img/read_message_close.png" alt="">
            </div>
            <div class="title bold">
                收货地址
            </div>
            <div class="insert-body ">
                <table>
                    <tr>
                        <td class="t-title">
                            <p>收货人<span></span></p>
                        </td>
                        <td class="t-main">
                            <input type="text" v-model="addEdit.truename">
                        </td>
                    </tr>
                    <tr>
                        <td class="t-title">
                            <p>所在地区<span></span></p>
                        </td>
                        <td class="t-main">
                            <ul>
                                <li>
                                    <select name="province" v-model="addEdit.province" @change="SetOption">
                                        <option v-for="(i,k) in provinces" :value="i.region_id" :key="k">
                                            {{i.region_name}}
                                        </option>
                                    </select>
                                </li>
                                <li><select name="city" v-model="addEdit.city" @change="SetOption">
                                    <option v-for="(i,k) in citys" :value="i.region_id" :key="k">{{i.region_name}}
                                    </option>
                                </select></li>
                                <li><select name="town" v-model="addEdit.town" @change="SetOption">
                                    <option v-for="(i,k) in towns" :value="i.region_id" :key="k">{{i.region_name}}
                                    </option>
                                </select></li>
                                <li><select name="county" v-model="addEdit.county" @change="SetOption">
                                    <option v-for="(i,k) in countys" :value="i.region_id" :key="k">{{i.region_name}}
                                    </option>
                                </select></li>
                            </ul>
                        </td>
                    </tr>
                    <tr>
                        <td class="t-title">
                            <p>详细地址<span></span></p>
                        </td>
                        <td class="t-main">
                            <input type="text" style="width: 480px" placeholder="请填写详细地址" v-model="addEdit.address">
                        </td>
                    </tr>
                    <tr>
                        <td class="t-title">
                            <p>手机号码<span></span></p>
                        </td>
                        <td class="t-main">
                            <input type="text" placeholder="请添加手机号码" v-model="addEdit.telephone">
                        </td>
                    </tr>
                    <tr>
                        <td class="t-title"> &nbsp; &nbsp;</td>
                        <td class="t-main">
                            <button class="cursor" @click="saveAddress">保存</button>
                        </td>
                    </tr>
                </table>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'add-address',
        data() {
            return {
                addEdit: {
                    truename: '',
                    telephone: '',
                    alladdress: '',
                    province: "",
                    provinceName: '',
                    city: '',
                    cityName: '',
                    town: '',
                    townName: '',
                    county: '',
                    countyName: ''
                },
                provinces: [],
                citys: [],
                towns: [],
                countys: []
            }
        },
        props:{
            add:{
                type:Boolean,
                default:false
            }
        },
        created(){
            this.getAddList(1, 'province');
        },
        methods:{
            SetOption(e) {
                let name = e.target.name;
                let value = e.target.value;
                switch (name) {
                    case 'province':
                        this.getAddList(value, 'city');
                        break;
                    case 'city' :
                        this.getAddList(value, 'town');
                        break;
                    case 'town':
                        this.getAddList(value, 'county');
                        break;
                    case 'county':
                        break;
                }
            },
            getAddList(pid, tag) {
                this.yum('api/city/lists', {pid: pid}).then(
                    res => {
                        if (res.data.status === 200) {
                            switch (tag) {
                                case 'province':
                                    this.provinces = res.data.data;
                                    break;
                                case 'city':
                                    this.citys = res.data.data;
                                    break;
                                case 'town':
                                    this.towns = res.data.data;
                                    break;
                                case 'county':
                                    this.countys = res.data.data;
                                    break;
                            }
                        } else {
                            this.bus.$emit('notice', res.data.msg)
                        }
                    }
                )
            },
            saveAddress() {
                let {truename, telephone, province, city, town, county, address, id} = this.addEdit;
                let data = {
                    truename: truename,
                    telephone: telephone,
                    province: province,
                    city: city,
                    town: town,
                    county: county,
                    address: address,
                    id: id,
                    act: id ? 'update' : 'add'
                };
                this.yum('api/address/save', data).then(res => {
                    if (res.data.status === 200) {
                        this.bus.$emit('add','1')
                        this.bus.$emit('notice', res.data.msg);
                    } else {
                        this.bus.$emit('add','1',500)
                        this.bus.$emit('notice', res.data.msg);
                    }
                })

            }
        }
    }
</script>

<style lang="stylus" scoped>
    .add-address
        position fixed
        width 100%
        height 100%
        line-height 100%
        min-width 1000px
        display block
        top 0
        left 0
        background-color rgba(0,0,0,0.5)
        text-align center
        vertical-align middle
        z-index 999
    .insert-address
        text-align left
        position relative
        vertical-align middle
        display inline-block
        min-height 350px
        background-color white
        box-shadow 0 0 8px #bbb
        padding 20px
        margin-top 15%
        width 666px
        .close
            position absolute
            width 20px
            height 20px
            right 5px
            top 5px
        .title
            height 50px
            line-height 50px
            border-bottom 1px solid #ccc
            font-size 16px
            padding 0 10px
            margin 0 10px
        table
            width 580px
            tr
                margin-top 10px
                height 40px
                display inline-block
            .t-title
                width 80px
                height 40px
                line-height 40px
                position relative
                color black
                p
                    text-align justify
                    height 40px
                    span
                        width 100%
                        display inline-block
            .t-main
                width 500px
                padding-left 20px
                input
                    display block
                    height 40px
                    line-height 40px
                    width 300px
                    padding 0 10px
                    outline none
                    border 1px solid #ccc
                    border-radius 4px
                button
                    width 100px
                    height 40px
                    font-size 16px
                    color white
                    background-color #ff0077
                    border none
                    outline none
                ul
                    position relative
                    height 40px
                    li
                        display inline-block
                        height 40px
                        width 70px
                        margin-right 10px
                        select
                            display block
                            width 70px
                            height 40px
                            outline none
                            border-radius 4px

</style>
